.fbnewscntr .newscntr .news-post{
    width: 75%;
    display: flex;
    gap: 10px;
    padding: 20px 20px;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0px 0px 12px 0px #bbd6ff;
}
.fbnewscntr .newscntr .news-post .news-postimg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
}
.fbnewscntr .newscntr .news-post .news-postimg img{
    width: 100%;
    height: 100%;
}
.fbnewscntr .newscntr .news-post .news-postinput{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;    
    padding-right: 5px;
}
.fbnewscntr .newscntr .news-post .news-postinput input{
    width: 100%;
    height: 80%;
    font-size: 17px;
    border-radius: 50px;
    padding-left: 15px;
    border: none;
    background-color: rgb(221, 221, 221);
}
.fbnewscntr .newscntr .news-post .news-postinput input:focus{
    outline: none;
}
.fbnewscntr .newscntr .news-post .news-postinput input::placeholder{
    font-size: 17px;
    color: rgb(134, 132, 132);
}
.fbnewscntr .newscntr .news-post .news-postinput svg{
    height: 15px;
    width: 15px;
    cursor: pointer;
}
.fbnewscntr .newscntr .news-post button{
    padding: 5px 25px;
    border: none;
    font-size: 15px;
    font-weight: 700;
    color: #ffffff;
    background-color: #5364ba;
    border-radius: 12px;
    cursor: pointer;
}