.fbnewscntr{
    flex: 1;
}
.fbnewscntr .newscntr{
    width: 100%;
    height: calc(100% - 60px);
    overflow: scroll;
    background-color:#f0f2f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0px;
    gap: 20px;
}
.fbnewscntr .newscntr::-webkit-scrollbar {
    display: none;
  }



  



.newsCard{
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 10px;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0px 0px 12px 0px #bbd6ff;
}
.newsCard .newsContainer{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 20px 10px 10px 20px;
}
.newsCard .newsContainer .news-postimg{
    width: 50px;
    height: 50px;
    border-radius: 12px;
    overflow: hidden;
}
.newsCard .newsContainer .news-postimg img{
    width: 100%;
    height: 100%;
}
.newsCard .newsContainer h2{
    font-weight: 500;
}
.newsCard .newsPostWrite{
    padding: 15px 20px;
}
.newsCard .newsPostWrite p{
    font-size: 20px;
}
.newsCard .newsField{
    width: 100%;
    max-height: 500px;
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0px 0px 12px 0px #232424;
}
.newsCard .newsField img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
}
.like-sectionh{
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logos-likes,
.cmnt-share{
    display: flex;
    align-items: center;
    gap: 5px;
}
.like-logo{
    background-color: rgb(241, 0, 133);
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.like-logo svg{
    width: 65%;
    height: 65%;
}
.logos-likes span,
.cmnt-share span,
.logo-txt span{
    cursor: pointer;
    font-size: 20px;
}
.logos-likes span:hover,
.cmnt-share span:hover,
.logo-txt span:hover{
    text-decoration: underline;
}
.gullu{
    padding: 5px 30px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    margin: 0px 15px;
    display: flex;
    justify-content: space-around;
}
.logo-txt{
    display: flex;
    align-items: center;
    gap: 8px;
}
.logo-txt svg{
    width: 20px;
    height: 20px;
    cursor: pointer;
}