.fbnavbar{
    width: 100%;
    height: 60px;
    display: flex;
    overflow: hidden;
}
.fbnavbar .plogo{
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #5564bf;
    cursor: pointer;
}
.fbnavbar .plogo svg{
    width: 15px;
    height: 15px;
}
.fbnavbar .psearch{
    flex: 1;
}
.fbnavbar .psearch input{
    height: 100%;
    width: 100%;
    border: 0px solid;
    padding-left: 20px;
    background-color: #5060b5;
    color: #ffffff;
}
.fbnavbar .psearch input::placeholder {
    color: rgb(255, 255, 255);
    opacity: 1; /* Firefox */
  }
  .fbnavbar .psearch input:focus{
    outline: none;
}



/* -----------message navbar container------------ */
.ruperivalutyena{
    background-color: #448aff;
    align-items: center;
    justify-content: space-between;
    padding: 0px 20px; 
}
.ruperivalutyena .pnav-messenger-name{
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 17px;
    color: #ffffff;
}
.ruperivalutyena svg{
    width: 20px;
    height: 20px;
    cursor: pointer;
}

/* ------------------news navbar css---------------- */
.bolnahalkehalke{
    width: 100%;
    height: 60px;
    background-color: #5364ba;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 30px;
    gap: 30px;
}
.bolnahalkehalke .pppicons{
    display: flex;
    align-items: center;
    gap: 20px;
}
.bolnahalkehalke .pppicons svg{
    width: 20px;
    height: 20px;
    cursor: pointer;
}
.bolnahalkehalke .ppprofile{
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
}
.bolnahalkehalke .ppprofile svg{
    width: 20px;
    height: 20px;
}
.bolnahalkehalke .ppprofile .profileimg{
    width: 30px;
    height: 30px;
    border-radius: 8px;
    overflow: hidden;
}
.bolnahalkehalke .ppprofile .profileimg img{
    width: 100%;
    height: 100%;
}