.fbmessage{
    width: 20%;
}
.fbmessage .messagescntr{
    width: 100%;
    height: calc(100% - 120px);
    overflow: scroll;
    padding: 10px 0px;
}
.fbmessage .messagescntr::-webkit-scrollbar {
    display: none;
  }
.messag-search-friend{
    height: 60px;
    display: flex;
    align-items: center;
    padding: 0px 10px;
    border-top: 5px solid #f0f2f5;
    border-bottom: 5px solid #f0f2f5;
}
.messag-search-friend input{
    /* flex: 1; */
    width: calc(100% - 30px);
    height: 100%;
    padding-left: 5px;
    font-size: 15px;
    border: none;
}
.messag-search-friend input:focus{
    outline: none;
}
.messag-search-friend input::placeholder{
    color: black;
}
.messag-search-friend svg{
    width: 30px;
    height: 30px;
    cursor: pointer;
}



.messages{
    display: flex;
    align-items: center;
    padding: 10px 20px;
    margin: 0px 5px;
    gap: 20px;
    border-radius: 12px;
    cursor: pointer;
}
.messages:hover{
    background-color: #f0f2f5;
}
.messages .msg-img{
    height: 60px;
    width: 60px;
    position: relative;
}
.messages .msg-img img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
}
.messages .msg-img .dotcntr{
    height: 25px;
    width: 25px;
    background-color: white;
    position: absolute;
    top: 100%;
    left: 100%;
    border-radius: 4px;
    transform: translate(-80%, -80%);
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.messages .msg-img .dotcntr .dotstatus{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: green;
}

.messages h3{
    font-weight: normal;
}