.pcntr{
    width: 25%;
    height: 100%;
}
.pcntr .fbpcntr{
    width: 100%;
    height: calc(100% - 60px);
    overflow: scroll;
}
.pcntr .fbpcntr::-webkit-scrollbar {
    display: none;
  }

/* ------------------------background profile image css---------------------- */

.bgimg{
    width: 100%;
    height: 180px;
    position: relative;
}
.bgimg img{
    width: 100%;
    height: 100%;
}
.bgimg .psqare{
    width: 80px;
    height: 80px;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.bgimg .psqare img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
}
.bgimg .psqare .pstatus{
    position: absolute;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    top: 100%;
    left: 100%;
    transform: translate(-70%, -70%);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
}
.bgimg .psqare .pstatus .pofstatus{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: green;
}


/* ------------------------pdetails css---------------------- */


.pnamehere{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 55px;
    gap: 10px;
}
.pintro{
    padding: 5px 15px;   
}
.pintro h3{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.pintro p{
    text-align: center;
}
.pintro .pphotos1{
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center; 
    gap: 5px;
    overflow: scroll;
}
.pintro .pphotos1::-webkit-scrollbar{
    display: none;
}
.pintro .pphotos1 img{
    width: 60px;
    height: 60px;
    border-radius: 12px;
}
.pintro .pplastcard{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: #ffffff;
    background-color: #a5d950;
    width: 60px;
    height: 60px;
    border-radius: 12px;
}
.pintro .pplastcard1{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: #ffffff;
    background-color: #ff00bf;
    width: 60px;
    height: 60px;
    border-radius: 12px;
}